.Column {
    border-radius:5px;
    border-top: 3px solid lightsalmon;
    box-shadow: 0px 0px 5px 5px #ccc;
    margin-right: 20px;
    width: 250px;
    text-align: center;
    background-color: white;
    min-height: 150px;
    overflow:hidden;
    height:1%;
}

.column-title{
    display: flex;
    justify-content: left;
    align-items: center;
    border-bottom: 1px solid #aaa;
    position: relative;
    height: 3em;

}

.edit-title{
    width: 80%;
    font-size: 1.5em;
    border: none;
    border-bottom: 2px solid white;
}
.edit-title:focus {
    transition: 0.6s;
    border-bottom: 2px solid red;
    outline: none;
}


.column-contents{
    min-height: 100px;
    padding: 0;
    list-style-type: none;

}

.drop{
    background-color: lightpink;
}

.delete{
    position: absolute;
    top: 0;
    right: 0;
    border: none;
}
