.Task {
    border-left: 2.5px solid rgb(51, 178, 220);
    margin: 5px;
    width: auto;
    font-size: small;
    /* border-radius: 5px; */
    height: 40px;
    display: flex;
    justify-content: left;
    align-items: center;
    position: relative;
}
.draggable-icon{
    background-image: url('drag-icon.png');
}

.task-edit {
    margin-left: 5px;
    border: none;
    border-bottom: white 2px solid;
    font-size: 1.1em
}
.task-edit:focus {
    transition: 0.6s;
    border-bottom: 2px solid red;
    outline: none;
}

.delete{
    position: absolute;
    top: 0;
    right: 0;
    border: none;
}
